const block0 = (context) => { return html`<div class="ui5-multi-combobox-root"	@focusin=${ifDefined(context.rootFocusIn)}	@focusout=${ifDefined(context.rootFocusOut)}	><span id="${ifDefined(context._id)}-hiddenText-nMore" class="ui5-hidden-text">${ifDefined(context.nMoreCountText)}</span><ui5-tokenizer slot="_beginContent"		show-more		class="ui5-multi-combobox-tokenizer"		?disabled="${ifDefined(context.disabled)}"		@ui5-showMoreItemsPress="${ifDefined(context._showMorePopover)}"		@ui5-tokenDelete="${ifDefined(context._tokenDelete)}"		@focusout="${ifDefined(context._tokenizerFocusOut)}"		?expanded="${ifDefined(context.expandedTokenizer)}"	>		${ repeat(context.items, undefined, (item, index) => block1(item, index, context)) }</ui5-tokenizer><input id="ui5-multi-combobox-input"		.value="${ifDefined(context.value)}"		inner-input		placeholder="${ifDefined(context.placeholder)}"		?disabled=${ifDefined(context.disabled)}		?readonly=${ifDefined(context.readonly)}		?required=${ifDefined(context.required)}		value-state="${ifDefined(context.valueState)}"		@input="${ifDefined(context._inputLiveChange)}"		@change=${ifDefined(context._inputChange)}		@keydown="${ifDefined(context._onkeydown)}"		@keyup="${ifDefined(context._onkeyup)}"		@focusin="${ifDefined(context._focusin)}"		@focusout="${ifDefined(context._focusout)}"		role="combobox"		aria-haspopup="listbox"		aria-labelledby="${ifDefined(context._id)}-hiddenText-nMore"></input>	${ !context.readonly ? block3(context) : undefined }<ui5-popover		class="ui5-multi-combobox-selected-items-popover"		horizontal-align="Stretch"		?no-arrow=${ifDefined(context.editable)}		placement-type="Bottom"><ui5-list separators="None" mode="${ifDefined(context.selectedItemsListMode)}"			@ui5-selectionChange=${ifDefined(context._listSelectionChange)}>			${ repeat(context.items, undefined, (item, index) => block4(item, index, context)) }</ui5-list></ui5-popover><ui5-popover class="ui5-multi-combobox-all-items-popover"		no-arrow		horizontal-align="Stretch"		initial-focus="ui5-multi-combobox-input"		horizontal-align="Left"		placement-type="Bottom"		@ui5-selectionChange=${ifDefined(context._listSelectionChange)}		@ui5-afterClose=${ifDefined(context._toggleIcon)}		@ui5-afterOpen=${ifDefined(context._toggleIcon)}><ui5-list separators="None" mode="MultiSelect" class="ui5-multi-combobox-all-items-list">			${ repeat(context._filteredItems, undefined, (item, index) => block6(item, index, context)) }</ui5-list></ui5-popover></div>`; };
