const block3 = (context) => { return html`<ui5-icon name="slim-arrow-down"			input-icon			slot="icon"			tabindex="-1"			@click=${ifDefined(context._showAllItemsPopover)}			?pressed="${ifDefined(context._iconPressed)}"			dir="${ifDefined(context.dir)}"		></ui5-icon>	`; };
